import React, { Component } from "react";
import Form from "./common/form";
import { getGenres } from "../services/genreService";
import { getMovie, saveMovie } from "../services/movieService";
import Joi from "joi-browser";
import { random } from "lodash";

class MovieForm extends Form {
  state = {
    data: {
      _id: "",
      title: "",
      dailyRentalRate: "",
      numberInStock: "",
      genreId: "",
    },
    genres: [],
    errors: {},
  };
  schema = {
    _id: Joi.string(),
    title: Joi.string().required().label("Title"),
    genreId: Joi.string().label("Genre"),
    numberInStock: Joi.number()
      .required()
      .min(0)
      .max(100)
      .label("Number in Stock"),
    dailyRentalRate: Joi.number()
      .required()
      .min(0)
      .max(10)
      .label("Daily Rental Rate"),
  };

  async populateGenres() {
    const data = await getGenres();
    data.json().then((genres) => {
      this.setState({ genres });
    });
  }
  async populateMovie() {
    try {
      const movieId = this.props.match.params.id;
      if (movieId === "new") return;
      const data = await fetch(
        "https://vidly-1dfd0-default-rtdb.firebaseio.com/movies.json/",
        {
          method: "GET",
        }
      );
      data.json().then((res) => {
        const movies = Object.values(res);
        console.log(movies);
        const movie = movies.find((movie) => {
          return movie._id === movieId;
        });
        this.setState({ data: this.mapToViewModel(movie) });
      });
    } catch (ex) {
      if (ex.response && ex.response.status === 404)
        return this.props.history.replace("/not-found");
    }
  }

  async componentDidMount() {
    await this.populateGenres();

    await this.populateMovie();
  }
  mapToViewModel(movie) {
    return {
      _id: movie._id,
      title: movie.title,
      genreId: movie.genre._id,
      numberInStock: movie.numberInStock,
      dailyRentalRate: movie.dailyRentalRate,
    };
  }
  doSubmit = async () => {
    const data = await saveMovie(this.state.data, this.state.genres);
    data.json().then((res) => {
      console.log(res);
    });
    this.props.history.push("/movies");
  };
  render() {
    const { id } = this.props.match.params;
    return (
      <div>
        <h1>{id === "new" ? "New movie" : "Edit"}</h1>
        <form onSubmit={this.handleSubmit}>
          {this.renderInput("title", "Title")}
          {this.renderSelect("genreId", "Genre", this.state.genres)}
          {this.renderInput("numberInStock", "Number in Stock", "number")}
          {this.renderInput("dailyRentalRate", "Rate", "number")}
          {this.renderButton("Save")}
        </form>
      </div>
    );
  }
}

export default MovieForm;
